import React, { Component } from 'react'
import PropTypes from 'prop-types'

export class Resume extends Component {
    

    render() {
        return (
            <div id="resumePart" className="resume">
                <div className="education">
                    <div className="title">
                        Education
                    </div>
                    <div className="item">
                        <h5 className="time-period">2017-2021</h5>
                        <span className="item-place">
                            Hanoi Open University (HOU)
                        </span>
                        <div className="item-info">
                            <h4>
                                Faculty of English
                            </h4>
                            <p>
                                Learn about English skills, reading and watching English documentary,and communicate in English
                            </p>
                        </div>
                    </div>
                    <div className="item">
                        <h5 className="time-period">2019-2020</h5>
                        <span className="item-place">
                            Vietpro Academy
                        </span>
                        <div className="item-info">
                            <h4>
                                Frontend Web Design
                            </h4>
                            <p>
                                Learn about HTML, CSS, JS and ReactJS
                            </p>
                        </div>
                    </div>
                </div>
                <div className="experience">
                    <div className="title">
                        Experience
                    </div>
                    <div className="item">
                        <h5 className="time-period">2019-Now</h5>
                        <span className="item-place">
                            Self-Study
                        </span>
                        <div className="item-info">
                            <h4>
                                Programing and Web Design
                            </h4>
                            <p>
                                Learn about C , C++ , Algorithm , Data Structures , Git , SQL all by myself
                            </p>
                        </div>
                    </div>
                </div>
                <div id="skills" className="skills">
                    <div className="title">
                        Skills
                    </div>
                    <div className="item">
                        <div className="item-info">
                            <p>
                                - Programming Languagues :  JavaScript, PHP ( basic ), C , C++
                            </p>
                            <p>
                                - Basic knowledge: Object Oriented Programming (OOP), HTML, CSS, Bootstrap, AntDesign , Git.
                            </p>
                            <p>
                                - Framework : ReactJS, Laravel ( basic)
                            </p>
                            <p>
                                - Platform : NodeJS
                            </p>
                            <p>
                                - Knowledgeable in English
                            </p>
                            <p>
                                - Willing to learn and adopt new technology into working
                            </p>
                        </div>
                    </div>
                </div>
            </div>
        )
    }
}

export default Resume
