import React, { Component } from 'react'
import PropTypes from 'prop-types'
import img from "../image/37046583_1469068633238535_8959388683824267264_o.jpg"

export class About extends Component {
    
    render() {
        return (
            <div className="container">
                <div className="about-me">
                        <div className="my-photo">
                            <img src={img}/>
                        </div>
                        <div className="home-text">
                            <div className="my-major">Intern ReactJS Developer</div>
                            <div className="my-full-name">Le Trong Duc</div>
                            <div className="my-profile">
                                <div className="birthday">
                                    <i class="fas fa-birthday-cake"></i>
                                    Jan 19 , 1999 
                                </div>
                                <div className="space">
                                    -
                                </div>
                                <div className="gender">
                                    <i class="fas fa-mars"></i>
                                    Male
                                </div>
                            </div>
                            <div className="introduce">
                                <p>
                                    To apply my knowledge in the field of Frontend and Javascript progamming into pratical use.
                                </p>
                                <p>
                                    Looking forward to working in a professional environment.
                                </p>
                                <p>
                                    To learn and to improve my professional experiences and other skills particularly of Javascript.
                                </p>
                            </div>
                            <div className="download-btn">
                                Download CV
                            </div>
                        </div>
                    
                </div>
            </div>
        )
    }
}

export default About
